function CompletionScreen(props) {
  function clicker() {
    window.location = 'https://johndejesus.com/attentiontraining';
  }
  return (
    <>
      <div class="app-title"><img className="me-2 mb-1" src={logo_icon}></img>Attention Training</div>

      <div class="app-desc"><h2 class="app-desc-title">Thanks for trying SATT!</h2>
        This was a fun prototype to create! Why? Well, the audio files are not just compiled .mp3s. Each sound effect and background music are their own files and this prototype sequences them live. This is great for experimental testing purposes where one would be interested in more control to examine on a fine-grain level (by block or sound selection) the <i>why and how</i> of components for optimization.
        <br/><br/>
        This supports a "building block" approach for intervention science. More specifically, ensuring the "optimal engineering" of each component before they are added to a multi-component intervention. Two exciting future questions are whether component effectiveness holds in combinations and for whom!
      
      </div>
      <Button type="submit" onClick={clicker} className="start-practice">Start again</Button>
      <div class="footer-link mt-3"><a class="fs-6 link-dark text-decoration-none " href="https://fullstackjohn.github.io">Return to fullstackjohn.github.io</a></div>


    </>
  );
}

